<template>
	<div id="single-blog">
		<h1 class="blog-title">{{blog.title}}</h1>
		<article>{{blog.content}}</article>
		<div class="author">
					<p>作者: {{blog.author}}</p>
					<p>分类:</p>
					<ul>
						<li v-for="category in blog.categories" :key="category">
						{{category}}
						</li>
					</ul>
		</div>

		<!-- <button @click="delblog()">删除</button> -->
	</div>
</template>

<script>
import axios from 'axios'
	export default{
		name:"single-blog",
		data(){
			return{
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			// this.$http.get('https://wd1182543348jfzvtq.wilddogio.com/posts/' + this.id + ".json")
			axios.get('https://wd9048853483dsiyoo.wilddogio.com/posts/' + this.id + ".json")
				.then((data) => {
					this.blog = data.data;
				})
		},
		methods:{
			delblog(){
				console.log(1)
			}
		}
	}
</script>
<style>
*{
  margin: 0;
  padding: 0;
}
	#single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background: #eee;
		border: 1px dotted #aaa;
		overflow: hidden;
	}
	.blog-title{
		text-align: center;
		margin-bottom: 20px;
	}
	#single-blog article{
		text-indent: 2em;
		line-height: 30px;
	}
	.author{
		margin-top: 30px;
		float: right;
		line-height: 30px;
	}
</style>


